"use client";
import { useState } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Send } from "lucide-react";
import axios from "axios";

export default function ChatBot() {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");

  const sendMessage = async () => {
    if (!input.trim()) return;
    const newMessage = { role: "user", content: input };
    setMessages([...messages, newMessage]);
    setInput("");

    const response = await axios.post(
      "/api/chat",
      { messages: [...messages, newMessage] },
      { headers: { "Content-Type": "application/json" } }
    );

    const data = await response.data;
    setMessages([...messages, newMessage, { role: "ai", content: data.reply }]);
  };

  return (
    <div className="flex flex-col h-screen mx-auto p-4 bg-gray-900 text-white rounded-lg shadow-lg border border-gray-700 custom-scrollbar">
      <h1 className="text-2xl font-semibold text-center mb-4 text-gray-200">AI-Powered Health Assistant</h1>
      <div className="flex-1 overflow-y-auto space-y-3 p-2 bg-gray-800 rounded-lg shadow-inner custom-scrollbar">
        {messages.map((msg, idx) => (
          <Card
            key={idx}
            className={`rounded-2xl p-3 shadow-md text-sm max-w-xs ${
              msg.role === "user" ? "bg-grey-500 text-white" : "bg-gray-700 text-gray-300"
            }`}
          >
            <CardContent>{msg.content}</CardContent>
          </Card>
        ))}
      </div>
      <div className="flex gap-3 mt-4 bg-gray-800 p-3 rounded-lg shadow-md">
        <Input
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Type a message..."
          className="flex-1 bg-gray-700 text-white border-none focus:ring-2 focus:ring-blue-400 rounded-lg p-2"
        />
        <Button onClick={sendMessage} className="p-3 bg-blue-500 hover:bg-blue-600 text-white rounded-lg shadow-md">
          <Send size={20} />
        </Button>
      </div>
    </div>
  );
}
